.rate-bar {
  position: absolute;
  right: 0;
  top: -100%;
  width: 6rem;
  height: 2rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 0.5rem;
}

.rate-option {
  height: 0.75rem;
  width: 0.75rem;
  top: 0;
  right: 0;
  border-radius: 2rem;
  z-index: 2;
  transition: width 150ms ease-in-out, height 150ms ease-in-out,
    background-color 250ms linear 150ms;
  display: flex;
  justify-content: center;
  align-items: center;
}

.option-icon {
  opacity: 0;
  position: relative;
  top: 0;
  left: 0;
}

.rate-option:hover {
  position: relative;
  overflow: visible;
  cursor: pointer;
  height: 5rem;
  width: 5rem;
  border-radius: 3rem;
  z-index: 3;
}

.rate-option:hover .option-icon {
  position: relative;
  opacity: 1;
}

.rate-option:hover#like-option,
.rate-option:hover#dislike-option,
.rate-option:hover#report-option {
  background-color: rgba(0, 0, 0, 0);
}

#like-option {
  background-color: #27c840;
}

#dislike-option {
  background-color: #fe5f57;
}

#report-option {
  background-color: #ffbc30;
}
